@import url('https://fonts.googleapis.com/css?family=Audiowide');

body {
    overflow-x: hidden;
    overflow-y: auto;
    background-color: #0d1019;
    font-family: 'Audiowide';
    margin: 0;
    padding: 0;
}

.create-org-container {
    display: inline-block;
    color: white;
    margin: auto;
    width: 99.5vw;
    height: 100vh;
    font-size: 3vh;
}

.register-form {
    margin: auto;
    display: flex;
    flex-direction: column;
    width: 30vw;
    min-width: 300px;
}

.input-title {
    margin-bottom: .5vh;
    margin-top: 2.5vh;
    font-size: 2.5vh;
}

.input-box {
    height: 2.5vh;
    font-family: 'Audiowide';
}

.button-container {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
}

.submit-button {
    font-size: 2.5vh;
    background: white;
    color: black;
    border-radius: 3vh;
    margin-top: 2.5vh;
    position: center;
    width: 20vw;
}

.submit-button:hover {
    color: white;
    background-color: #0984e3;
    cursor: pointer;
    transform: scale(1.15); 
    transition: ease-in-out .5s;
    animation: content_pulse 2s infinite;
}

p {
    text-align: center;
    font-size: 2vh;
}

.select-input {
    font-size: 2vh;
    height: 3.5vh;
    border-radius: 1.5vh;
    font-family: "Audiowide";
}

.logoDiv {
    position: relative;
    width: 20vh;
    top: 10px;
    height: 20vh;
    border: .25vh solid #1f2632;
    border-radius: 50vh;
    margin: auto;
}

.logoDiv .logo {

    width: 100%;
}

.logoDiv:hover {
    background-color: rgb( 9, 132, 227, .75);
    animation: content_pulse 2s infinite;
    cursor: pointer;
}

.alert-div {
    position: fixed;
    top: 0;
    z-index: 1;
    width: 100%;
    margin: auto;
    height: auto;
}

@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}