@import url('https://fonts.googleapis.com/css?family=Audiowide');

.endzone-btn {
    width: auto;
    height: 100%;
    min-height: 33px;
    font-size: 20px;
    font-weight: 100;
    border-radius: 2vh;
    color: white;
    border: 2px solid #222a37;
    background-color: #353b48;
}

.btn-body {
    width: 100%;
    height: 100%;
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
}

.btn-text {
    width: 100%;
    height: 100%;
    border-right: .5vh solid #0877cc;
    display: inline-flex;
    align-items: center;
    margin-right: 1vh;
}

.btn-text p {
    font-size: 18px;
    font-weight: 100;
    font-family: "audiowide";
    margin: auto;
    margin-right: 1vh;
    margin-left: 1vh;
}

.btn-icon {
    margin: 0;
    margin-top: .5vh;
    padding: 0;
}

.endzone-btn:hover {
    transform: scale(1.1);
    transition: .5s ease-in-out;
    cursor: pointer;
}

.endzone-btn:hover .btn-text {
    color: #0877cc;
}